const servicesForm = document.querySelectorAll('.services-continuation__item--button');
if(servicesForm.length > 0){
  const modalFormSale = document.querySelector('#modal-form-sale');
  const modalFormRent = document.querySelector('#modal-form-rent');
  const modalFormPurchase = document.querySelector('#modal-form-purchase');

  servicesForm.forEach(button => {
    button.addEventListener('click', (e) => {
      if(e.target.id == 'open-modal-sale'){
        modalFormSale.style.display = 'flex';
      }else if(e.target.id == 'open-modal-rent'){
        modalFormRent.style.display = 'flex';
      }else if(e.target.id == 'open-modal-purchase'){
        modalFormPurchase.style.display = 'flex';
      }
    })
    const closeModalFormSale = document.querySelector('#modal-form-sale .modal-form-sale__content--title-close');
    const closeModalFormRent = document.querySelector('#modal-form-rent .modal-form-sale__content--title-close');
    const closeModalFormPurchase = document.querySelector('#modal-form-purchase .modal-form-sale__content--title-close');

    closeModalFormSale.addEventListener('click', () => {
      modalFormSale.style.display = 'none';
    });
    closeModalFormRent.addEventListener('click', () => {
      modalFormRent.style.display = 'none';
    });    
    closeModalFormPurchase.addEventListener('click', () => {
      modalFormPurchase.style.display = 'none';
    });
    window.onclick = function(event) {
      if (event.target == modalFormSale) {
        modalFormSale.style.display = "none";
      }
      if (event.target == modalFormRent) {
        modalFormRent.style.display = 'none';
      }
      if (event.target == modalFormPurchase) {
        modalFormPurchase.style.display = 'none';
      }
    }
  })
}
let btnMenuHeader = document.querySelector('.header__container--button-burger');
let menuContainer = document.querySelector('.menu__box');
let hamburger = document.getElementById('hamburger');

if (btnMenuHeader && menuContainer && hamburger) {
  btnMenuHeader.addEventListener('click', () => {
    if (menuContainer.style.display === 'block') {
      // Если меню уже открыто, закрываем его
      menuContainer.style.display = 'none';
      hamburger.classList.remove('active');
      btnMenuHeader.classList.remove('active');
      document.body.style.overflow = 'auto';
    } else {
      // Если меню закрыто, открываем его
      menuContainer.style.display = 'block';
      hamburger.classList.add('active');
      btnMenuHeader.classList.add('active');
      document.body.style.overflow = 'hidden';
    }
  });
}

document.querySelectorAll('a[href^="#"]').forEach(anchor => {
  anchor.addEventListener('click', function(e) {
      e.preventDefault();
      menuContainer.style.display = 'none';
      hamburger.classList.remove('active');
      btnMenuHeader.classList.remove('active');


      const targetID = this.getAttribute('href');
      if(targetID !== '#'){
        const targetSection = document.querySelector(targetID);
  
        targetSection.scrollIntoView({
            behavior: 'smooth'
        });

      }
  }, false);
});


